.wrapContainer {
  padding: 8rem 2rem 8rem 2rem;

  @media (max-width: 360px) {
    padding: 4rem 2rem 4rem 2rem;
  }
}


.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0;

  h1 {
    text-align: center;
    font-size: 3.125rem;
    padding-bottom: 3rem;
    font-weight: 400;
  }

  h2 {
    padding-bottom: 20px;
    font-weight: 400;
  }

  .imageWrapper {
    text-align: center;

    img {
      max-width: 100%;
    }
  }

  .linkWrapper {
    a {
      color: rgb(239, 91, 76);
      font-weight: 500;
      cursor: pointer;
      transition: 0.3s;
      &:hover {
        color: rgb(210,67,53);
        transition: 0.3s;
      }
    }
  }

  @media (max-width: 767px) {
    h2 {
      padding-top: 30px;
      font-size: 1.5rem;
      text-align: center;
    }
    h1 {
      font-size: 2rem;
    }
    .linkWrapper {
      text-align: center;
    }
  }
  @media (max-width: 360px) {
    .col {
      padding: 0;
    }
  }
}




