body, html {
    font-family: 'Open Sans', sans-serif !important;
    margin: 0;
    height: 100%;
}

.row {
    padding-left: 0 !important;
    padding-right: 0 !important;
}

.is-invalid {
    background-image: none !important;
}

.custom-radio label {
    font-weight: normal !important;
}

.modal {
    top: 200px !important;
    padding-right: 0 !important
}

.modal-footer button {
    width: 5.5rem;
    height: 40px;
    border-radius: 5px;
    outline: none;
    box-shadow: none;
    border: none;
    color: white;
    background: rgb(239, 91, 76);
}

.modal-open {
    padding-right: 0 !important;
}

.active {
    color: #1d68c3 !important;
}

#section4 {
    min-height: 100vh;
}

.custom-file-label {
    overflow: hidden;
}
.disabled {
    background-color: #d7d7d7 !important;
    cursor: default !important;

}


